import "./contact.css";
import Phone from "../../img/phone.png";
import Email from "../../img/email.png";
import Address from "../../img/address.png";
import { useContext, useRef, useState } from "react";
import emailjs from "emailjs-com";
import { ThemeContext } from "../../context";

const Contact = () => {
  const formRef = useRef();
  const [done, setDone] = useState(false)
  const theme = useContext(ThemeContext);
  const darkMode = theme.state.darkMode;

  const handleSubmit = (e) => {
    e.preventDefault();
    emailjs
      .sendForm(
        "service_rrvnzco",
        "template_3v5nih4",
        formRef.current,
        "user_DrriDPTGKO2Zj4RDXCA6W"
      )
      .then(
        (result) => {
          console.log(result.text);
          setDone(true)
        },
        (error) => {
          console.log(error.text);
        }
      );
  };

  return (
    <div className="c">
      <div className="c-bg"></div>
      <div className="c-wrapper">
        <div className="c-left">
          <h1 className="c-title">Hablemos de tu proyecto</h1>
          <div className="c-info">
            <div className="c-info-item">
              <img src={Phone} alt="" className="c-icon" />
              +56954247771
            </div>
            <div className="c-info-item">
              <img className="c-icon" src={Email} alt="" />
              jorgerioschile@gmail.com
            </div>
            <div className="c-info-item">
              <img className="c-icon" src={Address} alt="" />
              Santiago de Chile
            </div>
          </div>
        </div>
        <div className="c-right">
          <p className="c-desc">
            <b>Cuentame  que necesitas</b> Contactame, siempre estare disponible para un proyecto adecuado
            y con gusto respondere a la brevedad
            
          </p>
          <form ref={formRef} onSubmit={handleSubmit}>
            <input style={{backgroundColor: darkMode && "#333"}} type="text" placeholder="Nombre" name="user_name" />
            <input style={{backgroundColor: darkMode && "#333"}} type="text" placeholder="Motivo" name="user_subject" />
            <input style={{backgroundColor: darkMode && "#333"}} type="text" placeholder="Correo" name="user_email" />
            <textarea style={{backgroundColor: darkMode && "#333"}} rows="5" placeholder="Mensaje" name="message" />
            <button>Submit</button>
            {done && "Gracias"}
          </form>
        </div>
      </div>
    </div>
  );
};

export default Contact;
